import socketIOClient from 'socket.io-client';
import { useEffect, useState } from 'react'
import TeamScoreBoard from './TeamScoreBoard'
import PlayerScoreBoard from './PlayerScoreBoard'

const ENDPOINT = "http://localhost:3000"

const socket = socketIOClient('/', { transport: ['websocket'] })

const ScoreBoard = () => {

    const [gameStats, setGameStats] = useState({})

    useEffect(() => {

        socket.on('updateScoreBoard', (stats) => {
            console.log(stats)
            setGameStats(stats)
        })

    }, [])

    if (!gameStats?.hasOwnProperty('blueTeamStats')) return <p>No data found</p>

    return (
        <div>
            <TeamScoreBoard blueTeamStats={gameStats.blueTeamStats} redTeamStats={gameStats.redTeamStats} />
            <PlayerScoreBoard blueTeam={true} teamStats={gameStats.blueTeamStats} teamMetadata={gameStats.blueTeamMetadata} />

            <PlayerScoreBoard blueTeam={false} teamStats={gameStats.redTeamStats} teamMetadata={gameStats.redTeamMetadata} />
        </div>
    )


}

export default ScoreBoard