const TeamScoreBoard = (props) => {




    return (
        <div className="TeamScoreBoard">
            <div className="BlueTeamScoreBoard">
                <table className="vertical-center beautiful-text">
                    <tr>
                        <td>{props.blueTeamStats.totalGold} <img src={goldIcon} width="25px"></img>   </td>
                        <td>{props.blueTeamStats.totalKills} <img src={killsIcon} width="25px"></img>   </td>
                        <td>{props.blueTeamStats.dragons.length} <img src={dragonIcon} width="25px"></img>   </td>
                        <td>{props.blueTeamStats.towers} <img src={towersIcon} width="25px"></img>   </td>
                        <td>{props.blueTeamStats.barons} <img src={baronsIcon} width="25px"></img>   </td>
                        <td>{props.blueTeamStats.inhibitors} <img src={inhibIcon} width="25px"></img></td>
                    </tr>
                </table>
                <div><span style={{ position: 'absolute', top: '100px' }}>{props.blueTeamStats.dragons.map(el=>{return getDragonIcon(el)})}  &nbsp; </span></div>
            </div>
            <div className="Live">
                <p align="center" className="vertical-center beautiful-text">LIVE</p>
            </div>
            <div className="RedTeamScoreBoard">
                <table className="vertical-center beautiful-text">
                    <td>&nbsp;<img src={inhibIcon} width="25px"></img>{props.redTeamStats.inhibitors}</td>
                    <td><img src={baronsIcon} width="25px"></img>{props.redTeamStats.barons} </td>
                    <td><img src={towersIcon} width="25px"></img>{props.redTeamStats.towers} </td>
                    <td><img src={dragonIcon} width="25px"></img>{props.redTeamStats.dragons.length} </td>
                    <td><img src={killsIcon} width="25px"></img>{props.redTeamStats.totalKills} </td>
                    <td><img src={goldIcon} width="25px"></img>{props.redTeamStats.totalGold} </td>
                </table>
                <span style={{ position: 'absolute', top: '100px' }}>{props.redTeamStats.dragons.map(el=>{return getDragonIcon(el)})} </span>
            </div>
        </div>

    )


}

export default TeamScoreBoard