import React from 'react';
import { useEffect, useState } from 'react'
import GamesDropbox from './GamesDropbox';
import LeagueDropbox from './LeagueDropbox';
import ScoreBoard from './ScoreBoard';

function GameInfo(props) {
    
    const [isLoading, setIsLoading] = useState(true)
    const [gameList, setGameList] = useState([])
    const [selectedGame,setSelectedGame] = useState('')
    const ENDPOINT = "http://localhost:3000"

    useEffect(() => {
        fetch('/games').then(resp => resp.json()).then(data => {
            setGameList(data)
            setIsLoading(false)
            fetch('/setgame/'+data[0].id).then(resp => resp.json()).catch(err => console.log(err))
            setSelectedGame(data[0].id)
        })
    }, [])

    useEffect(() =>{
        fetch('/setgame/'+selectedGame).then(resp => resp.json()).catch(err => console.log(err))
        console.log(selectedGame)
    },[selectedGame])

    const changeSelectedGame = (event) =>{
        setSelectedGame(event.currentTarget.value)
    }

    if(isLoading){
        return <div>Loading...</div>
    }


    return (
        <div>
            <GamesDropbox gameList={gameList} selectedGame={selectedGame} changeSelectedGame={changeSelectedGame}/>
            <ScoreBoard/>
        </div>

    )
};

export default GameInfo;