import '../styles/PlayerScoreBoard.css'

const PlayerScoreBoard = (props) => {

    let transformedPlayer = props.teamStats.participants.map((el, index) => {
        let meta = props.teamMetadata.participantMetadata[index]
        let imgUrl = `https://opgg-static.akamaized.net/images/lol/champion/${meta.championId}.png`
        if (props.blueTeam) {
            return (
                <tr>
                    <td>{meta.summonerName.split(' ').slice(1).join(' ')}  </td>
                    <td><img src={imgUrl} width="50px"></img>  </td>
                    <td>{el.level}  </td>
                    <td>{el.totalGold}  </td>
                    <td>&nbsp;&nbsp;{el.kills}&nbsp;/&nbsp;{el.deaths}&nbsp;/&nbsp;{el.assists}</td>
                    <td>{el.creepScore}  </td>
                    <td><progress id="health" value={el.currentHealth} max={el.maxHealth} data-label={`${el.currentHealth} / ${el.maxHealth}`}></progress></td>
                </tr>


            )
        } else {
            return (
                <tr>
                    <td><progress id="health" value={el.currentHealth} max={el.maxHealth} data-label={`${el.currentHealth} / ${el.maxHealth}`}></progress></td>
                    <td>{el.creepScore}  </td>
                    <td>{el.kills}&nbsp;/&nbsp;{el.deaths}&nbsp;/&nbsp;{el.assists}&nbsp;&nbsp;</td>
                    <td>{el.totalGold}  </td>
                    <td>{el.level}  </td>
                    <td><img src={imgUrl} width="50px"></img>  </td>
                    <td>{meta.summonerName.split(' ').slice(1).join(' ')}  </td>
                </tr>
            )

        }
    })

    if (props.blueTeam)
        return (
            <div className="PlayerScoreBoard">
                <div className="UniquePlayerScoreBoard">
                    <table>
                        <tr>
                            <th>Name</th>
                            <th>Champion</th>
                            <th>Level</th>
                            <th>Gold</th>
                            <th>KDA</th>
                            <th>Farm</th>
                            <th>HP</th>
                        </tr>
                        {transformedPlayer}
                    </table>

                </div>
            </div>


        )
    else return (
        <div className="PlayerScoreBoard">
            <div className="UniquePlayerScoreBoard" id="redTeam">
                <table>
                    <tr>
                        <th>HP</th>
                        <th>Farm</th>
                        <th>KDA</th>
                        <th>Gold</th>
                        <th>Level</th>
                        <th>Champion</th>
                        <th>Name</th>
                    </tr>
                    {transformedPlayer}
                </table>
            </div>
        </div>

    )

}

export default PlayerScoreBoard