import React, { useEffect, useState } from 'react'

const GamesDropbox = (props) => {




    return (
        <div>
            <select onChange={props.changeSelectedGame}>
                {props.gameList.map(game =>{
                    return <option value={game.id} key={game.id}>{game.match}</option> 
                })}
            </select>
        </div>
    )

}

export default GamesDropbox